const Page1 = (props) => {
	return (
		<>
			<div className="info">
				<h2>Welcome! First things first...</h2>
				<h4>you can always change later</h4>
			</div>
			<div className="inputForm">
				<div className="details">
					<label>First Name</label>
					<input type="text" placeholder="Robert Downey Jr." />
				</div>
				<br />
				<div className="details">
					<label>Display Name</label>
					<input type="text" placeholder="Iron Man" />
				</div>
			</div>
			<button onClick={props.submit}>Create Workspace</button>
		</>
	);
};

export default Page1;
