import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import Page1 from "./Page/Page1";
import Page2 from "./Page/Page2";
import Page3 from "./Page/Page3";
import Page4 from "./Page/Page4";
import "./style/Form.css";

const Form = () => {
	const [tracker, setTracker] = useState(1);
	const [second, setSecond] = useState("circle ");
	const [third, setThird] = useState("circle ");
	const [four, setFour] = useState("circle ");
	let line = document.getElementById("line");
	const submit = () => {
		setTracker((prev) => {
			return prev + 1;
		});
	};
	useEffect(() => {
		if (tracker === 2) {
			setSecond((prev) => prev + "active noborder");
			line.style.width = "50%";
		} else if (tracker === 3) {
			setThird((prev) => prev + "active noborder");
			line.style.width = "75%";
		} else if (tracker === 4) {
			setFour((prev) => prev + "active noborder");
			line.style.width = "100%";
		}
	}, [tracker]);

	return (
		<div className="container">
			<h3>cutShort</h3>
			<div className="Circle_container">
				<div className="linecontainer"></div>
				<div id="line" className="line"></div>
				<div className="circle active noborder">1</div>
				<div className={second}>2</div>
				<div className={third}>3</div>
				<div className={four}>4</div>
			</div>
			{tracker === 1 && <Page1 submit={submit} />}
			{tracker === 2 && <Page2 submit={submit} />}
			{tracker === 3 && <Page3 submit={submit} />}
			{(tracker === 4 || tracker >= 4) && <Page4 submit={submit} />}
		</div>
	);
};

export default Form;
