import "../style/Page2.css";
const Page2 = (props) => {
	return (
		<>
			<div className="info">
				<h2>Let's set up a home for all...</h2>
				<h4>you can always create another workspace later</h4>
			</div>
			<div className="inputForm">
				<div className="details">
					<label>Workspace Name</label>
					<input type="text" placeholder="Robert Downey Jr." />
				</div>
				<br />
				<div className="details">
					<label>Display Name</label>
					<div className="inputexamplecontainer">
						<input
							type="text"
							disabled
							className="disabledinput"
							placeholder="    www.eden.com/"></input>
						{/* <hr /> */}
						<input type="text" placeholder="Iron Man" />
					</div>
				</div>
			</div>
			<button onClick={props.submit}>Create Workspace</button>
		</>
	);
};

export default Page2;
